<template lang="pug">
  .pd20
    <div class="navigate-bar">
      <span>测试账号审核</span>
    </div>
    el-form.table-search-form(size="medium" :model="searchForm" :inline="true")
      el-form-item(label="审核状态 ：" )
        <el-radio-group v-model="searchForm.approved" @change="reloadTable">
          <el-radio-button label="">全部</el-radio-button>
          <el-radio-button label="0">待审核</el-radio-button>
          <el-radio-button label="-1">审核失败</el-radio-button>
          <el-radio-button label="1">测试中</el-radio-button>
          <el-radio-button label="2">测试通过</el-radio-button>
          <el-radio-button label="-2">测试失败</el-radio-button>
        </el-radio-group>
    gh-table(ref="ghTable" url="/test/account/getAccountTestList" :searchForm="searchForm")
      <el-table-column prop="companyName" label="供应商企业名称" />
      <el-table-column prop="contractCreditCode" label="统一社会信用代码" width="180"/>
      <el-table-column prop="contactType" label="对接类型" >
        <div slot-scope="scope">
          // <span v-if="scope.row.contactType == 1" type="warning">设备对接</span>
          <span v-if="scope.row.contactType == 2" type="success">系统对接</span>
          <span v-if="scope.row.contactType == 3" type="danger">软硬件适配</span>
        </div>
      </el-table-column>
      <el-table-column prop="account" label="账号" />
      <el-table-column prop="secretKey" label="密钥" />
      <el-table-column prop="businessContact" label="业务联系人" />
      <el-table-column prop="businessContactPhone" label="业务联系人电话" />
      <el-table-column prop="approved" label="状态">
        <div slot-scope="scope">
          <el-tag v-if="scope.row.approved == 0" type="warning">待审核</el-tag>
          <el-tag v-if="scope.row.approved == -1" type="danger">审核失败</el-tag>
          <el-tag v-if="scope.row.approved == 1" type="info">测试中</el-tag>
          <el-tag v-if="scope.row.approved == 2" type="success">测试通过</el-tag>
          <el-tag v-if="scope.row.approved == -2" type="danger">测试失败</el-tag>
        </div>
      </el-table-column>
      <el-table-column prop="address" label="操作" width="290" fixed="right">
        <div class="table-cell-ctrl" slot-scope="scope">
          <a href="javascript:;" @click="showDetail(scope.row.uuid)">详情</a>
          // <router-link :to="{path:'/examination',query:{uuid:scope.row.uuid}}">日志</router-link>
          <a v-if="(+scope.row.approved) == 0 || (+scope.row.approved) == -1" @click="examCompany(scope.row.uuid,scope.row.name,1)">通过审核</a>
          <a v-if="+scope.row.approved == 1 || ((+scope.row.approved) == -2)" @click="examCompany(scope.row.uuid,scope.row.name,2)">通过测试</a>
          <a v-if="+scope.row.approved == 0 " @click="examCompany(scope.row.uuid,scope.row.name,-1)">拒绝审核</a>
          <a v-if="+scope.row.approved == 1 " @click="examCompany(scope.row.uuid,scope.row.name,-2)">未通过测试</a>
          <a @click="delCompany(scope.row.uuid,scope.row.name)">删除</a>
        </div>
      </el-table-column>

    el-dialog(
      :title="dialog.approved == 2 ? '测试通过' : (dialog.approved == 1 ? '审核通过' :'审核拒绝')"
      center append-to-body :visible.sync="dialog.show" width="550px")

      .dialog-tip
        | 确定{{dialog.approved == -1 ? '拒绝' : '通过'}}
        span.dialog-tip-name {{dialog.companyName}}
        | 的{{dialog.approved == 2 ? '测试' : '审核'}}？
      el-form(label-position="right" label-width="100px"  :model="dialog.form" ref="form")
        el-form-item(label="账户" v-if="dialog.approved == 1")
          el-input(v-model="dialog.form.account")
        el-form-item(label="密钥" v-if="dialog.approved == 1")
          el-input(v-model="dialog.form.secretKey")
        el-form-item(label="数据提交地址" v-if="dialog.approved == 1")
          el-input(v-model="dialog.form.dataAddr")
        el-form-item(label="备注")
          el-input(type="textarea" :rows="3" v-model="dialog.form.note")
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog.show = false">取 消</el-button>
        <el-button type="primary" @click="examSubmit">确 定</el-button>
      </span>

    el-dialog(
      title="测试账号详情"
      center append-to-body :visible.sync="detailDialog.show" width="550px")
      <el-form label-position="right" label-width="190px" :model="dialog.form" ref="form" style="width:500px;">
        <el-form-item label="测试项目名称" prop="projectName" verify >
          span {{detailDialog.form.projectName}}
          // <el-input v-model="detailDialog.form.projectName" disabled/>
        </el-form-item>
        <el-form-item label="项目地址" prop="projectAddr" verify>
          span {{detailDialog.form.projectAddr}}
          // <el-input v-model="detailDialog.form.projectAddr" disabled/>
        </el-form-item>
        <el-form-item label="总承包名称" prop="contractName" verify>
          span {{detailDialog.form.contractName}}
          // <el-input v-model="detailDialog.form.contractName" disabled/>
        </el-form-item>
        <el-form-item label="总承包统一社会信用代码" prop="contractCreditCode" verify>
          span {{detailDialog.form.contractCreditCode}}
          // <el-input v-model="detailDialog.form.contractCreditCode" maxLength="18" disabled/>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="detailDialog.show = false">关闭</el-button>
      </span>

</template>
<script>
import ghTable from '@/components/table'
export default {
  components: { ghTable },
  data () {
    return {
      searchForm: {
        approved: ''
      },
      detailDialog: {
        show: false,
        form: {}
      },
      dialog: {
        show: false,
        companyUuid: '',
        companyName: 'test',
        approved: 1,
        form: {
          account: '',
          secretKey: '',
          dataAddr: '',
          note: ''
        }
      }
    }
  },
  methods: {
    reloadTable () {
      console.log(this.searchForm)
      this.$nextTick(() => {
        this.$refs.ghTable.reloadTableList()
      })
    },
    showDetail (uuid) {
      this.$ajax.post('/test/account/getAccountTestByUuid', { uuid })
        .then((res) => {
          this.detailDialog.form = res.result
          this.detailDialog.show = true
        })
        .catch((e) => {
          console.error(e)
          this.$message.error(e.msg || '获取详情失败')
        })
    },
    examCompany (companyUuid, companyName, approved) {
      Object.assign(this.dialog.form, { account: '', secretKey: '', note: '' })
      Object.assign(this.dialog, { companyName, companyUuid, approved, show: true })
    },
    examSubmit () {
      let url = '/test/account/checkAccountTest'
      let { companyUuid: uuid, approved } = this.dialog
      this.$ajax.post(url, Object.assign({}, this.dialog.form, { uuid, approved }))
        .then(() => {
          this.$refs.ghTable.reloadTableList()
          this.$message.success('提交成功')
          this.dialog.show = false
        })
        .catch((e) => {
          this.$message.error(e.msg || '服务器繁忙,提交失败!')
        })
    },
    delCompany (uuid, companyName) {
      this.$confirm(`确认删除${companyName}？`)
        .then((res) => {
          console.log(res)
          this.$ajax.post('/test/account/delete', { uuid })
        })
        .then(() => {
          this.$refs.ghTable.reloadTableList()
          this.$message.success('删除成功')
        })
        .catch((e) => {
          if (e == 'cancel') {
            return
          }
          this.$message.error(e.msg || '服务器繁忙,删除失败!')
        })
    }
  }
}
</script>
<style lang="scss">
@import "~@/styles/_variables";
.dialog-tip {
  margin-bottom: 20px;
  color: #333;
  font-size: 16px;
  &-name {
    color: $orange;
    padding: 0 5px;
  }
}
</style>
